import axios from 'axios'
import qs from 'qs'
import router from '../router/router';
import { parseServerApiAddress, parseSoftwareList, topThree, excludeTopThree, clickSoftwareDetails as modulePage, showClientLoginDlg, shellOpenPage, postPopPage, compareArray, getAuth, getModuleList } from './common'
import bus from '../assets/eventBus'
import $ from 'jquery'
import { mapState } from 'vuex'
import store from '../store';
import { types } from './mutationTypes'
import mixinForClient from './mixin'
export default {
    name: 'ReMen',
    mixins: [mixinForClient],
    data() {
        return {
            imgArray: [],
            carouselHeight: '',
            newTableData: [],//最新
            hotTableData: [], //收藏
            likeTableData: [], //浏览
            isLogin: false, //登录状态
            loadingStatus: 0, //0不显示loading 3显示loading
            newLoading: false,
            collectLoading: false,
            browseLoading: false,
            curClickModuleId: null,
            _act: ''
        }
    },

    computed: {
        ...mapState([
            'keepAlive',
            'loginChange'
        ])
    },

    watch: {
        loadingStatus(newVal, oldVal) {
            if (newVal == 3) {
                store.dispatch('loading', { isChange: false });
            }
        },

        loginChange(newVal, oldVal) {
            if (newVal != oldVal && newVal) {
                let that = this;
                getAuth(
                    function (serApiAddr) {
                        that.isLogin = serApiAddr.islogin;
                    },
                    function (code, msg) {
                        console.log(code + msg);
                    }
                );
            }
            if (this.$route.fullPath == '/reMen' && !newVal) {
                this.loadingStatus = 0;
            }
            if (newVal != oldVal && !newVal && this.$route.fullPath.indexOf('reMen') != -1) {
                if (this.curClickModuleId) {
                    var i = 0, j = 0, k = 0;
                    var obj;
                    for (; i < this.newTableData.length; i++) {
                        if (this.curClickModuleId == this.newTableData[i].id) {
                            obj = this.newTableData[i];
                            break;
                        }
                    }
                    if (i == this.newTableData.length) {
                        for (j = 0; j < this.hotTableData.length; j++) {
                            if (this.curClickModuleId == this.hotTableData[j].id) {
                                obj = this.newTableData[j];
                                break;
                            }
                        }
                        if (j == this.hotTableData.length) {
                            for (k = 0; k < this.likeTableData.length; k++) {
                                if (this.curClickModuleId == this.likeTableData[k].id) {
                                    obj = this.newTableData[k];
                                    break;
                                }
                            }
                        }
                    }
                    if (obj) {
                        if (this._act == 'detail') {
                            modulePage(obj);
                        } else if (this._act == 'collect') {
                            if (obj.collection) {
                                var strPrompt = "收藏成功，可在“我的-收藏”里面查看和管理哦~";
                                this.$message({
                                    message: strPrompt,
                                    type: 'success',
                                    duration: 2000
                                })
                            } else {
                                this.clickSoftwareCollection(obj);
                            }
                        }
                    }
                    this.curClickModuleId = null;
                    this._act = '';
                }
            }
        },

        newTableData(newVal, oldVal) {
            if (!compareArray(newVal, oldVal)) {
                //登陆状态改变后做处理
                if (!this.$store.state.loginChange) {
                    this.newLoading = false;
                }
                if (this.$route.fullPath == '/reMen') {
                    if (this.$store.state.loginChange) {
                        this.loadingStatus++;
                    }
                }
            }
        },

        hotTableData(newVal, oldVal) {
            if (!compareArray(newVal, oldVal)) {
                //登陆状态改变后做处理
                if (!this.$store.state.loginChange) {
                    this.collectLoading = false;
                }
                if (this.$route.fullPath == '/reMen') {
                    if (this.$store.state.loginChange) {
                        this.loadingStatus++;
                    }
                }
            }
        },

        likeTableData(newVal, oldVal) {
            if (!compareArray(newVal, oldVal)) {
                //登陆状态改变后做处理
                if (!this.$store.state.loginChange) {
                    this.browseLoading = false;
                }
                if (this.$route.fullPath == '/reMen') {
                    if (this.$store.state.loginChange) {
                        this.loadingStatus++;
                    }
                }
            }
        },

    },

    beforeRouteEnter(to, from, next) {
        next(vm => {
            if (vm.$store.state.keepAlive) {
                to.meta.keepAlive = true;
                store.commit(types.ALIVE, { isAlive: false });
                setTimeout(() => {
                    vm.$refs.newTb.scrollTop = to.meta.scrollTopNew;
                    vm.$refs.collect.scrollTop = to.meta.scrollTopCollect;
                    vm.$refs.browse.scrollTop = to.meta.scrollTopBrowse;
                    vm.imgLoad();
                }, 100);
                //防止因reload导致原页面无数据而重新请求数据
                if (vm.newTableData.length == 0) {
                    vm.getNewDataList(20);
                }
                if (vm.hotTableData.length == 0) {
                    vm.getHotDataList(20);
                }
                if (vm.likeTableData.length == 0) {
                    vm.getLikeDataList(20);
                }
                next();
            } else {
                if (!vm.$route.meta.keepAlive) {
                    vm.initData();
                    vm.$nextTick(() => {
                        vm.imgLoad();
                    })
                    next();
                }
            }
        })
    },

    beforeRouteLeave(to, from, next) {
        this.$route.meta.scrollTopNew = this.$refs.newTb.scrollTop;
        this.$route.meta.scrollTopCollect = this.$refs.collect.scrollTop;
        this.$route.meta.scrollTopBrowse = this.$refs.browse.scrollTop;
        this.$route.meta.keepAlive = false;

        next();
    },

    mounted: function () {
        if (!this.$route.meta.keepAlive) {
            this.initData();
        }
        window.addEventListener('resize', () => {
            this.imgLoad();
        }, false);
        this.imgLoad();
    },

    created: function () {
        bus.$on('template', (msg) => {
            if (msg == 1) {
                console.log("监听到了模板数据已完成初始化！");
                this.initData();
            }
        })
    },

    methods: {
        initData() {
            this.getNewDataList(20);
            this.getHotDataList(20);
            this.getLikeDataList(20);
            this.getAdvertisement();
            let that = this;
            getAuth(
                function (serApiAddr) {
                    that.isLogin = serApiAddr.islogin;
                },
                function (code, msg) {
                    console.log(code + '-' + msg);
                }
            );
        },

        indexMethod(index) {
            return index + 4;
        },

        topThree: topThree,

        excludeTopThree: excludeTopThree,

        //点击详情
        clickSoftwareDetails(row) {
            let that = this;
            // 跳转
            modulePage(row);
        },

        //点击收藏
        clickSoftwareCollection(row) {
            let that = this;
            getAuth(
                function (serApiAddr) {
                    if (!serApiAddr.islogin) {
                        //没有登录,调用客户端登录
                        showClientLoginDlg(router.currentRoute.path);
                        that.curClickModuleId = row.id;
                        that._act = "collect";
                    } else {
                        var urlPlat = JSON.parse(sessionStorage.getItem('serverMap')).cadsvrplatserver;
                        urlPlat += "/api/CADSvrPlat/UserCollection/UpdateCollectionState/" + serApiAddr.tokenid;
                        //请求参数
                        var paramData = new Object();
                        paramData.CollectionId = row.id;
                        paramData.Type = 0;
                        paramData.CollectionState = !row.collection;
                        axios.post(urlPlat, qs.stringify(paramData))
                            .then(res => {
                                row.collection = !row.collection;
                                var strPrompt = row.collection ? "收藏成功，可在“我的-收藏”里面查看和管理哦~" : "取消收藏成功";
                                that.$message({
                                    message: strPrompt,
                                    type: 'success',
                                    duration: 2000
                                })
                            })
                            .catch(res => {
                                console.log(res);
                            });
                    }
                },
                function (code, msg) {
                    console.log(code + '-' + msg);
                }
            );
        },

        //仅显示前3行
        getRowStyle({
            row,
            rowIndex
        }) {
            //console.log(row);
            if (rowIndex > 1) {
                return {
                    display: 'none'
                };
            } else {
                return {};
            }
        },

        //最新排行
        getNewDataList(nLimit) {
            let that = this;
            const nLimitAll = 1000;
            getAuth(
                function (serApiAddr) {
                    var urlPlat = JSON.parse(sessionStorage.getItem('serverMap')).cadsvrplatserver;
                    //文件相关参数
                    var filterParam = {
                        StartIndex: 0,
                        Limit: nLimit,
                        WhereCondition: [
                            {
                                FieldCondition: [{
                                    Name: "State",
                                    QueryType: 20,
                                    Value: 1, //已启用授权
                                    DataType: 30
                                }]
                            }
                        ],
                        OrderSetting: [
                            {
                                Name: "CreateDate",
                                IsASC: false
                            }
                        ]
                    };
                    var compare = function (obj1, obj2) {
                        var dt1 = new Date(obj1.CreateDate);
                        var dt2 = new Date(obj2.CreateDate);
                        if (dt1.getTime() > dt2.getTime()) {
                            return -1;
                        } else if (dt1.getTime() < dt2.getTime()) {
                            return 1;
                        } else {
                            return 0;
                        }
                    }

                    var softList = new Array();
                    getModuleList(filterParam, serApiAddr.tokenid, serApiAddr.aestokenid, serApiAddr.islogin,
                        function (res, tokenid, aestokenid) {
                            softList = parseSoftwareList(res, tokenid, aestokenid);
                            softList.sort(compare);
                            for (var i = 0; i < softList.length; i++) {
                                if (i < 3) {
                                    softList[i].icon = "./Img/icons/" + (i + 1) + ".jpg";
                                }
                            }
                            that.newTableData = softList;
                        },
                        function (res) {
                            if (undefined != that.newTableData && null != that.newTableData) {
                                for (var i = 0; i < res.length; ++i) {
                                    if (serApiAddr.userid == res[i].UserId) {
                                        for (var j = 0; j < that.newTableData.length; ++j) {
                                            if (res[i].CollectionId == that.newTableData[j].id) {
                                                that.newTableData[j].collection = true;
                                                break;
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        null,
                        null,
                    );
                },
                function (code, msg) {
                    console.log(code + "-" + msg);
                }
            );
        },

        //收藏排行
        getHotDataList(nLimit) {
            let that = this;
            const nLimitAll = 1000;
            getAuth(
                function (serApiAddr) {
                    var urlPlat = JSON.parse(sessionStorage.getItem('serverMap')).cadsvrplatserver;
                    //文件相关参数
                    var filterParam = {
                        StartIndex: 0,
                        Limit: nLimit,
                        WhereCondition: [
                            {
                                FieldCondition: [
                                    {
                                        Name: "CollectionNum",
                                        QueryType: 40,
                                        Value: 1,
                                        DataType: 30
                                    }
                                ]
                            }
                        ],
                        OrderSetting: [
                            {
                                Name: "CollectionNum",
                                IsASC: false
                            }
                        ],
                        IsGetUsers: true
                    };
                    var compare = (obj1, obj2) => {
                        if (obj1.CollectionNum > obj2.CollectionNum) {
                            return -1;
                        } else if (obj1.CollectionNum < obj2.CollectionNum) {
                            return 1;
                        } else {
                            return 0;
                        }
                    }

                    var softList = new Array();
                    getModuleList(filterParam, serApiAddr.tokenid, serApiAddr.aestokenid, serApiAddr.islogin,
                        function (res, tokenid, aestokenid) {
                            softList = parseSoftwareList(res.List, tokenid, aestokenid);
                            softList.sort(compare);
                            for (var i = 0; i < softList.length; i++) {
                                if (i < 3) {
                                    softList[i].icon = "./Img/icons/" + (i + 1) + ".jpg";
                                }
                            }
                            that.hotTableData = softList;
                        },
                        function (res) {
                            if (undefined != that.hotTableData && null != that.hotTableData) {
                                for (var i = 0; i < res.length; ++i) {
                                    if (serApiAddr.userid == res[i].UserId) {
                                        for (var j = 0; j < that.hotTableData.length; ++j) {
                                            if (res[i].CollectionId == that.hotTableData[j].id) {
                                                that.hotTableData[j].collection = true;
                                                break;
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        null,
                        null,
                    );
                },
                function (code, msg) {
                    console.log(code + "-" + msg);
                }
            );
        },

        //浏览排行(浏览次数最多)
        getLikeDataList(nLimit) {
            let that = this;
            const nLimitAll = 1000;
            getAuth(
                function (serApiAddr) {
                    var urlPlat = JSON.parse(sessionStorage.getItem('serverMap')).cadsvrplatserver;
                    //文件相关参数
                    var filterParam = {
                        StartIndex: 0,
                        Limit: nLimit,
                        WhereCondition: [
                            {
                                FieldCondition: [{
                                    Name: "BrowseNum",
                                    QueryType: 40, //>=
                                    Value: 1,
                                    DataType: 30 //整数
                                }]
                            }
                        ],
                        OrderSetting: [
                            {
                                Name: "BrowseNum",
                                IsASC: false
                            }
                        ],
                        IsGetUsers: true
                    };

                    var compare = function (obj1, obj2) {
                        if (obj1.BrowseNum > obj2.BrowseNum) {
                            return -1;
                        } else if (obj1.BrowseNum < obj2.BrowseNum) {
                            return 1;
                        } else {
                            return 0;
                        }
                    };

                    var softList = new Array();
                    getModuleList(filterParam, serApiAddr.tokenid, serApiAddr.aestokenid, serApiAddr.islogin,
                        function (res, tokenid, aestokenid) {
                            softList = parseSoftwareList(res.List, tokenid, aestokenid);
                            softList.sort(compare);
                            for (var i = 0; i < softList.length; i++) {
                                if (i < 3) {
                                    softList[i].icon = "./Img/icons/" + (i + 1) + ".jpg";
                                }
                            }
                            that.likeTableData = softList;
                        },
                        function (res) {
                            if (undefined != that.likeTableData && null != that.likeTableData) {
                                for (var i = 0; i < res.length; ++i) {
                                    if (serApiAddr.userid == res[i].UserId) {
                                        for (var j = 0; j < that.likeTableData.length; ++j) {
                                            if (res[i].CollectionId == that.likeTableData[j].id) {
                                                that.likeTableData[j].collection = true;
                                                break;
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        null,
                        null,
                    );
                },
                function (code, msg) {
                    console.log(code + "-" + msg);
                }
            );
        },

        //获取广告图片
        getAdvertisement() {
            var devEnv = false;
            let that = this;
            getAuth(
                function (serApiAddr) {
                    var urlPlat = JSON.parse(sessionStorage.getItem('serverMap')).cadsvrplatserver;
                    var urlAddrImg = urlPlat + "/api/CADSvrPlat/Common/GetAdvertise/" + serApiAddr.tokenid;
                    that.$http.get(urlAddrImg).then(function (res) {
                        if (undefined != res.data.DataContext) {
                            var arrImg = res.data.DataContext;
                            if (null != arrImg || undefined != arrImg) {
                                this.imgArray.length = 0;
                                for (var i = 0; i < arrImg.length; i++) {
                                    var obj = new Object();
                                    //2.拼接图片地址
                                    obj.src = JSON.parse(sessionStorage.getItem("serverMap")).leadingwebserver + arrImg[i].src;
                                    obj.url = (arrImg[i].url == null || arrImg[i].url == undefined) ? "" : arrImg[i].url;
                                    if (devEnv) {
                                        obj.url = obj.url.replace("https://app.jiansheyun.com.cn", JSON.parse(sessionStorage.getItem("serverMap")).leadingwebserver);
                                    }
                                    
                                    this.imgArray.push(obj);
                                }
                            }
                        }
                    }, function (res) {
                        console.log(res);
                    })
                },
                function (code, msg) {
                    console.log(code + "-" + msg);
                }
            );
        },

        //点击广告连接打开新的浏览器窗口
        createNewPage(url) {
            if (url.indexOf("/T/") < 0) {
                var obj = new Object();
                obj.Url = url;
                shellOpenPage(JSON.stringify(obj));
            } else {
                getAuth(
                    function (serApiAddr) {
                        var obj = new Object();
                        obj.Url = url + "?tokenid=" + serApiAddr.aestokenid;
                        postPopPage(JSON.stringify(obj));
                    },
                    function (code, msg) {
                        console.log(code + "-" + msg);
                    }
                );
            }
        },

        //广告图片加载后布局调整
        imgLoad() {
            if (undefined != this.$refs.carouselHeight && this.$refs.carouselHeight.length > 0) {
                this.carouselHeight = this.$refs.carouselHeight[0].width * 500 / 1920; //原图宽*高=1920*500 /2250*500
            }
            this.$nextTick(() => {
                this.updateUi_rb();
            })
        },

        updateUi_rb() {
            this.$nextTick(() => {
                var clientHeight = window.innerHeight - 16;
                var ht = 240;
                ht = clientHeight - $(".top_").outerHeight(true) - parseInt($(".pos_left").css('padding-top')) -
                    parseInt($(".pos_left").css('padding-bottom')) - $(".tableHeader").outerHeight(true) -
                    parseInt($(".outBorder").css('margin-top')) - 10;//走马灯指示器在外侧的话-26
                var tabReMen = {
                    height: ht + 'px'
                };

                $(".scroll-y").css(tabReMen);
            })
        }

    }
}